/* Navigation Bar */
.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background-color: #007bff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .nav-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    height: 60px;
  }
  
  .logo {
    color: white;
    text-decoration: none;
    font-size: 20px;
    font-weight: bold;
  }
  
  /* Desktop Navigation Links */
  .nav-list {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
    gap: 20px;
  }
  
  .nav-link {
    color: white;
    text-decoration: none;
    font-size: 16px;
    transition: color 0.3s ease;
  }
  
  .nav-link:hover {
    color: #d4eaff;
  }
  
  /* Sign-In Button */
  .sign-in-button {
    background-color: white;
    color: #007bff;
    border: none;
    padding: 8px 15px;
    border-radius: 20px;
    font-weight: bold;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease;
  }
  
  .sign-in-button:hover {
    background-color: #f0f0f0;
  }
  
  /* Dropdown Menu */
  .dropdown-menu {
    position: absolute;
    top: 60px;
    right: 10px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    z-index: 1001;
    width: 200px;
    list-style-type: none;
    padding: 10px 0;
    display: flex;
    flex-direction: column;
  }
  
  /* Dropdown Items */
  .dropdown-item {
    padding: 10px 15px;
    font-size: 16px;
    color: #007bff;
    text-decoration: none;
    text-align: left;
    cursor: pointer;
    border-bottom: 1px solid #ddd;
    transition: background-color 0.2s ease-in-out;
  }
  
  .dropdown-item:last-child {
    border-bottom: none;
  }
  
  .dropdown-item:hover {
    background-color: #f1f1f1;
  }
  
  /* Mobile View */
  .menu-toggle {
    display: none;
    background-color: white;
    border: none;
    font-size: 20px;
    padding: 8px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  @media (max-width: 768px) {
    .nav-list {
      display: none;
      flex-direction: column;
      background-color: white;
      position: absolute;
      top: 60px;
      right: 10px;
      width: 90%;
      border-radius: 10px;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
      padding: 10px 0;
    }
  
    .nav-list.open {
      display: flex;
    }
  
    .nav-link {
      color: #007bff;
      padding: 10px;
      text-align: center;
    }
  
    .menu-toggle {
      display: block;
    }
  }
  